body {
  margin: 0;
  font-family: "Inter",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #23252C;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{
width: 330px;
border: 2px solid black;
background-color: white;
margin: 2% auto;
border-radius: 16px;
}
info{
  text-align: center;

}
.info-div{
  margin: 0 auto;
}
info h1{
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 3px;
}
info h3{
  margin-top: 3px;
  color: #297FE5;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 3px;
}
info a{
  margin-top: 3px;
  font-size: 11px;
  margin-bottom: 10px;
}
img{
  max-width: 330px;
  border-top-right-radius: 16px;
  border-top-left-radius: 16px;

}
button{
  padding: 0.6em 0.8em;
  width: 115px;
  font-size: 14px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.219);
  background-color: white;
  font-family: "Inter",sans-serif;
  letter-spacing: 0.55px;
}
button + button{
  margin-left: 1.5em;
}
.blue-button{
  background-color: #297FE5;
  color: white;
}
button:hover{
  cursor: pointer;
  filter: brightness(0.9);
}
.about{
  text-align: left;
  padding: 0em 1.5em;
}
.about h2{
  font-size: 19px;
  margin-bottom: 3px;
}
.about p{
  margin-top: 3px;
  margin-bottom: 0;
  font-size: 12px;
  color: #4A4E74;
  line-height: 18px;
}
section{
  padding-top: 0;
  margin-top: 0.5em;
}
section + section{
  margin-top: 0em;
}
footer{
  margin: 2.5em auto 0 auto;
  text-align: center;
  background-color: #D5D4D8;
  padding: 0.8em 0;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}
.socials{
  cursor: pointer;
  width: 50px;
  font-size: 28px;
  color: black;
}
.socials:hover{
  filter: brightness(0.8);
}